html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

* { -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a { text-decoration: none; color: inherit; }

img { max-width: 100%; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

@font-face { font-family: "proximanova-regular"; src: url("../fonts/proximanova-regular-webfont.woff2") format("woff2"), url("../fonts/proximanova-regular-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: "proximanova-bold"; src: url("../fonts/proximanova-bold-webfont.woff2") format("woff2"), url("../fonts/proximanova-bold-webfont.woff") format("woff"); font-weight: normal; font-style: normal; font-display: swap; }

strong { font-weight: 700; }

small { font-size: inherit; }

em, i { font-style: normal; }

h1, h2, h3, h4, h5, h6 { font-weight: 400; }

.h2, h2 { font-size: 24px; font-family: TimesNewRoman, serif; color: #003366; text-align: center; }

body { background: #FFF; line-height: 1.4; font-size: 16px; font-family: Arial, sans-serif; color: #000; }

input, textarea { font-family: inherit; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder { font-family: inherit; }

input::placeholder, textarea::placeholder { font-family: inherit; }

.icon { display: inline-block; fill: currentColor; font-size: inherit; }

.out { width: 1300px; margin-left: auto; margin-right: auto; }

.bg { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-position: 50% 0%; position: fixed; height: 100%; width: 1920px; left: 0; top: 0; z-index: -1; }

.content { width: 1200px; margin-left: auto; margin-right: auto; background-color: rgba(255, 255, 255, 0.7); border-radius: 10px; margin-top: -30px; padding-top: 30px; padding-bottom: 30px; z-index: 2; position: relative; }

.content__inner { background-color: #fff; padding-left: 15px; padding-right: 15px; }

.content__top { padding-left: 15px; padding-right: 15px; }

.hr, hr { background-color: #d5d5d5; border: 0; margin: 0; height: 6px; margin-bottom: 20px; }

.btn { color: #fff; display: inline-block; text-align: center; text-transform: uppercase; background-color: #c91426; padding: 7px 14px; transition: box-shadow 0.3s; font-size: 12px; }

.no-touch .btn:hover { box-shadow: 0 0 5px #000; }

.header { height: 105px; display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.header__logo { width: 340px; }

.header__title { width: 520px; color: #15356f; font-size: 24px; font-style: italic; font-family: TimesNewRoman, serif; }

.header__courses { width: 439px; text-align: right; }

.courses { background-image: url("../img/01-fon-kurs.png"); padding-top: 5px; width: 165px; padding-left: 5px; padding-bottom: 10px; font-size: 15px; line-height: 1.2; text-align: center; border-radius: 5px; margin-left: auto; }

.nav ul { font-size: 0; }

.nav li { display: inline-block; vertical-align: top; width: 11.11111%; font-size: 14px; background: #333; background-image: linear-gradient(bottom, #222222 0%, #555555 100%); position: relative; z-index: 1; opacity: .94; }

.nav li:first-child { border-radius: 3px 0 0 3px; }

.nav li:first-child a { border-left: 0; }

.nav li:last-child a { border-right: 0; }

.no-touch .nav li:hover a { background: #333; color: #fff; text-shadow: 0 1px 0 #000; box-shadow: inset 0 0 10px #111; opacity: 0.9; }

.nav a { color: #ccc; font-weight: 700; font-size: 14px; text-shadow: 0 -1px 0 #000; display: block; text-align: center; height: 50px; line-height: 50px; border-right: 1px solid #222; border-left: 1px solid #555; transition: all 0.3s; }

.no-touch .nav li:hover ul { transform: translate(0, 0); overflow: visible; width: 100%; opacity: 1; visibility: visible; }

.nav li ul { visibility: hidden; opacity: 0; transition: all 0.3s; position: absolute; z-index: 1; top: 100%; left: 0; width: 0; overflow: hidden; }

.nav li ul li { display: block; width: 100%; opacity: .99; }

.nav li ul li a { background: #f3f3f3 !important; text-shadow: 0 -1px 0 #eee !important; line-height: 36px; height: 40px; color: #555 !important; border-top: 1px solid #fff; border-left: 0 !important; border-right: 0 !important; font-size: 12px; box-shadow: none !important; }

.no-touch .nav li ul li a:hover { background: #ffd !important; color: #111 !important; text-shadow: 0 1px 0 #fff !important; }

.nav li ul li:nth-child(odd) a { background: #eee !important; }

.hero__nav { position: relative; z-index: 3; }

.slider.has-no-transition .slick-track { transition: 0s !important; }

.slider__wrap { position: relative; font-size: 0; }

.slider__prev, .slider__next { position: absolute; top: 50%; transform: translate(0, -50%); width: 40px; height: 80px; background-color: rgba(0, 0, 0, 0.7); }

.slider__prev::before, .slider__next::before { content: ''; width: 24px; height: 24px; border-left: 8px solid #fff; border-bottom: 8px solid #fff; transform: rotate(45deg); display: inline-block; position: absolute; top: 28px; left: 10px; }

.slider__prev { left: 0; }

.slider__next { right: 0; transform: translate(0, -50%) rotate(180deg); }

.slider__fullscreen { position: absolute; top: 10px; right: 10px; background-image: url("../img/full.svg"); background-repeat: none; display: inline-block; width: 24px; height: 24px; background-color: rgba(0, 0, 0, 0.7); border-radius: 3px; }

.slide__img { height: 435px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.popup { position: fixed; width: 100%; height: 100%; top: 0; left: 0; z-index: 10; background-color: rgba(0, 0, 0, 0.7); display: flex; justify-content: center; align-items: center; flex-direction: row; flex-wrap: nowrap; visibility: hidden; opacity: 0; transition: opacity 0.3s, visibility 0.3s; }

.popup.is-active { visibility: visible; opacity: 1; }

.popup__inner { width: 100%; }

.items { display: flex; justify-content: flex-start; align-items: normal; flex-direction: row; flex-wrap: nowrap; margin-left: -15px; margin-right: -15px; }

.item { width: calc(33.33333% - 15px); text-align: center; margin-left: 15px; margin-right: 15px; position: relative; overflow: hidden; display: block; }

.no-touch .item:hover .item__cover { visibility: visible; opacity: 1; }

.no-touch .item:hover .item__cover-title { transform: translate(0, 0); }

.no-touch .item:hover .item__cover-text { transform: translate(0, 0); }

.no-touch .item:hover .item__cover-btn { opacity: 1; }

.item__title { color: #003366; font-size: 18px; font-weight: 700; display: block; }

.item__img { height: 180px; width: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; display: block; }

.item__cover { visibility: hidden; opacity: 0; position: absolute; top: 0; left: 0; width: 100%; height: 100%; z-index: 1; background-color: rgba(117, 117, 117, 0.97); padding-top: 20px; padding-bottom: 5px; transition: opacity 0.3s, visibility 0.3s; }

.item__cover-title { text-transform: uppercase; color: #fff; font-size: 17px; background-color: rgba(48, 47, 47, 0.8); padding: 10px; transform: translate(0, -150%); transition: transform 0.3s; }

.item__cover-text { padding: 10px 20px 20px; font-style: italic; color: #fff; font-size: 14px; font-family: Georgia, serif; transform: translate(0, 150%); transition: transform 0.3s; }

.item__cover-btn { opacity: 0; transition: opacity 0.5s; }

.item--tour .item__title { text-transform: uppercase; font-size: 13px; color: #fff; margin-bottom: 10px; background-color: transparent; }

.item--tour .item__text { font-size: 12px; color: #fff; }

.item--tour .item__meta { background-color: rgba(0, 0, 0, 0.7); display: block; }
